export default {
  props: {
    data: {
      type: Array,
      default: null,
    },
  },
  data() {
    return {
      search: '',
    }
  },
  computed: {
    list() {
      if (this.search !== '') {
        return this.data.filter((box) => {
          return box.name.toLowerCase().includes(this.search.toLowerCase())
        })
      }
      return this.data
    },
  },
}
