export default {
  layout: 'error-layout',
  props: {
    error: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      pageNotFound: 'Page you are looking for could not be found.',
      otherError: 'An error occurred',
      title404: '404 error',
    }
  },
  head() {
    const title =
      this.error.statusCode === 404 ? this.title404 : this.otherError
    return {
      title,
    }
  },
}
